.App {
  max-width: 860px;
  margin: 40 px auto;
  text-align: center;
}

button{
  background: none;
  border: 2px solid #ffff;
  padding: 6px 12px;
  border-radius: 4px;
  color: #ffff;
  font-weight: bold;
  cursor: pointer;
  font-size: 1em;
}
 button:hover {
  background: #c23866;
  color: #fff;
 }

 .card-grid {
    margin-top: 40px;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 20px;
    
 }